.technologies-swiper {
  width: 80%;
  height: 150px;
  overflow: hidden;
  position: relative;
  margin: 0 auto;
}

.swiper-container {
  display: flex;
  width: 100%;
  height: 100%;
}

.tech-slide {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  background-color: #f4f4f4;
  flex: 0 0 calc(20% - 2em);
  margin-right: 2em;
  transition: transform 0.8s ease-in-out;
}
.tech-slide:hover {
  transform: scale(1.05);
}

.tech-slide img {
  max-width: 80px;
  height: auto;
  -o-object-fit: contain;
     object-fit: contain;
  border-radius: 8px;
  margin-bottom: 5px;
  transition: transform 0.3s ease-in-out;
}
.tech-slide img:hover {
  transform: scale(1.1);
}

.tech-slide p {
  font-size: 0.8rem;
  font-weight: bold;
  color: #333;
  margin: 0;
  text-transform: capitalize;
  letter-spacing: 1px;
  padding-top: 5px;
  word-wrap: break-word;
  text-align: center;
}

@media (max-width: 1200px) {
  .tech-slide {
    flex: 0 0 calc(25% - 2em);
  }
}
@media (max-width: 768px) {
  .tech-slide {
    flex: 0 0 calc(33.33% - 2em);
  }
}
@media (max-width: 480px) {
  .tech-slide {
    flex: 0 0 calc(50% - 1em);
  }
}/*# sourceMappingURL=technologieSwiper.css.map */