// Variables pour les couleurs et la typographie
$primary-color: #4caf50;
$secondary-color: #f4f4f4;
$hover-effect: 0.05;
$font-size: 1.1rem;
$font-weight-bold: bold;
$border-radius: 10px;
$shadow-color: rgba(0, 0, 0, 0.1);

// Mixin pour les animations de transition
@mixin transition-effect($property: all, $duration: 0.3s, $ease: ease-in-out) {
  transition: $property $duration $ease;
}

// Container du Swiper
.technologies-swiper {
  width: 80%; // Fixe la largeur à 80% de l'écran
  height: 150px; // Fixer la hauteur du swiper
  overflow: hidden; // Masquer les éléments qui débordent
  position: relative;
  margin: 0 auto; // Centrer le swiper horizontalement
}

// Conteneur principal des slides (avec flex pour un défilement horizontal)
.swiper-container {
  display: flex;
  width: 100%;
  height: 100%;
}

// Slide individuel ajusté à la hauteur
.tech-slide {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 10px;
  box-shadow: 0 4px 10px $shadow-color;
  border-radius: $border-radius;
  background-color: $secondary-color; // Fond léger pour chaque technologie
  flex: 0 0 calc(20% - 2em); // Chaque slide prend 20% de la largeur moins l'espace de 2em
  margin-right: 2em; // Espace de 2em entre les slides
  @include transition-effect(transform, 0.8s, ease-in-out);

  &:hover {
    transform: scale(1 + $hover-effect); // Effet de zoom sur le slide
  }
}

// Images des technologies ajustées
.tech-slide img {
  max-width: 80px; // Limiter la largeur de l'image
  height: auto;    // Hauteur auto pour maintenir les proportions
  object-fit: contain;
  border-radius: 8px;
  margin-bottom: 5px;
  transition: transform 0.3s ease-in-out;
  
  &:hover {
    transform: scale(1.1); // Effet de zoom léger au survol de l'image
  }
}

// Texte sous chaque image
.tech-slide p {
  font-size: 0.8rem; // Taille de police réduite pour s'adapter à la hauteur
  font-weight: $font-weight-bold;
  color: #333;
  margin: 0;
  text-transform: capitalize;
  letter-spacing: 1px;
  padding-top: 5px;
  word-wrap: break-word; // S'assurer que le texte ne déborde pas
  text-align: center;
}

// Responsivité : Ajuster le nombre de slides visibles selon la taille de l'écran
@media (max-width: 1200px) {
  .tech-slide {
    flex: 0 0 calc(25% - 2em); // 4 slides visibles à la fois
  }
}

@media (max-width: 768px) {
  .tech-slide {
    flex: 0 0 calc(33.33% - 2em); // 3 slides visibles à la fois
  }
}

@media (max-width: 480px) {
  .tech-slide {
    flex: 0 0 calc(50% - 1em); // 2 slides visibles à la fois
  }
}